<template>
  <div class="personal">
    <PersonalHeader></PersonalHeader>
    <div class="loginBg">
      <div class="loginBox">
        <h2>用户登录</h2>
        <div class="row">
          <i class="iconfont icon-phone"></i>
          <input type="text" v-model="phone" placeholder="手机号码">
        </div>
        <div class="row">
          <i class="iconfont icon-yanzheng"></i>
          <input type="text" v-model="imgcode" placeholder="请输入图片验证码">
          <img class="picYz" @click="changeCaptcha" :src="captcha">
        </div>
        <div class="row">
          <i class="iconfont icon-message"></i>
          <input type="text" v-model="code" placeholder="请输入短信验证码">
          <span class="messageYz" @click="getCode" v-if="showCode">获取验证码</span>
          <span class="messageYz" v-else>{{outTime}}S后重新获取</span>

        </div>
        <div class="loginBtn" @click="getLogin">登 录</div>
        <div class="remarks">
          <el-checkbox v-model="checked"></el-checkbox>
          <span>同意加盟指南<b>《服务协议》</b></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import api from "@/api/login-api"

  export default {
    name: "personal",
    data(){
      return {
        phone:'',
        imgcode:'',
        code:'',
        captcha:'',
        checked: true,
        showCode:true,
        outTime:60
      }
    },
    created(){
      this.getData();
    },
    methods:{
      getData(){
        api.getImageCode().then(data => {
          this.captcha = data
        });
      },
      changeCaptcha:function(){
        api.getImageCode().then(data => {
          this.captcha = data
        });
      },
      getCode:function(){
        const {phone} = this;
        const _this = this;
        if(!this.validatePhone(phone)){
          return
        }
        if(!this.imgcode){
          this.$message.error('请输入图片验证码');
          return
        }

        api.getCode({phone,img_code:this.imgcode}).then(data => {
          this.showCode = false;
          let intervalID = setInterval(function(){
            if(_this.outTime == 0){
              _this.showCode = true;
              _this.outTime = 60;
              clearInterval(intervalID)
            }
            _this.outTime--;
          },1000)
          this.$message({message:'验证码发送成功',type:'success'});
        });
      },
      validatePhone: function(phone){
        if(phone.length != 11){
          this.$message.error('手机号码格式不正确');
          return false;
        }
        return true;
      },
      getLogin:function () {
        const {checked,imgcode,code,phone} = this;
        if(!this.validatePhone(phone)){

        }else if( !imgcode){
          this.$message.error('请输入图片验证码');
        }else if( !code){
          this.$message.error('请输入短信验证码');
        }else if(checked==false){
          this.$message.error('请先阅读服务协议');
        }else{
          api.login({img_code:imgcode,code,phone}).then(data =>{
              localStorage.setItem('token',data.token);
              localStorage.setItem("user",JSON.stringify( data.user));
              this.$router.push({path: '/'});
          });
        }
      }
    },
    components: {
      PersonalHeader
    }
  }
</script>

<style scoped type="text/less" lang="less">
  .loginBg{
    width: 100%;
    height: 657px;
    padding-top: 123px;
    background: url("../../../static/image/loginBg.jpg") center no-repeat;
  }
  .loginBox{
    width: 346px;
    height: 415px;
    padding: 85px 0 0 657px;
    margin: 0 auto;
    background: url("../../../static/image/logicPic.jpg") left center no-repeat #fff;
    h2{
      font-size: 26px;
      color: #333;
      font-family: '黑体';
      margin-bottom: 40px;
    }
    .row{
      width: 286px;
      height: 42px;
      line-height: 42px;
      border: 1px solid #D9D9D9;
      padding-left: 12px;
      margin-bottom: 13px;
      overflow: hidden;
      display: flex;
      flex-direction: row;
      i{
        display: inline-block;
        vertical-align: middle;
        color: #ccc;
        font-size: 22px;
        margin-right: 12px;
        position: relative;
        top: -1px;
      }
      input{
        display: inline-block;
        vertical-align: middle;
        width: 140px;
        height: 42px;
        line-height: 42px;
        position: relative;
        top: -1px;
      }
      .picYz{
        width: 114px;
        height: 42px;
        cursor: pointer;
      }
      .messageYz{
        width: 114px;
        height: 20px;
        line-height: 20px;
        color: #5389F7;
        margin-top: 12px;
        text-align: center;
        border-left: 1px solid #D9D9D9;
        cursor: pointer;
      }
    }
    .loginBtn{
      width: 300px;
      height: 44px;
      line-height: 44px;
      color: #fff;
      font-size: 16px;
      border-radius: 2px;
      background: -webkit-gradient(linear, 100% 0, 0 0, from(#579EFA), to(#4F74F4));
      background: -webkit-linear-gradient(to bottom, #579EFA, #4F74F4);
      background: -moz-linear-gradient(to bottom, #579EFA, #4F74F4);
      background: -o-linear-gradient(to bottom, #579EFA, #4F74F4);
      background: linear-gradient(to bottom, #579EFA, #4F74F4);
      text-align: center;
      margin-top: 36px;
      cursor: pointer;
    }
    .remarks{
      display: flex;
      flex-direction: row;
      margin-top: 18px;
      .el-checkbox{
        margin-right: 6px;
        position: relative;
        top: 2px;
      }
      b{
        font-weight: normal;
        color: #5389F7;
      }
    }
  }


</style>



